import React from "react";
import Navbar from "../navbar/Navbar";
import "./Trip.css";

function Trip() {
  return (
    <div>
      <Navbar />
      <div className="Trip">
        <h3>Our Tavel Packages</h3>
        <table class="table table-striped">
          <thead class="thead-dark">
            <tr>
              <th scope="col">Packages</th>
              <th scope="col">Room</th>
              <th scope="col">Single</th>
              <th scope="col">Double</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th scope="row">Hotel 1</th>
              <td>Prince Suite</td>
              <td>5000</td>
              <td>9000</td>
            </tr>
            <tr>
              <th scope="row">Hotel 2</th>
              <td>Premium Suite</td>
              <td>4000</td>
              <td>7000</td>
            </tr>
            <tr>
              <th scope="row">Hotel 3</th>
              <td>Super Delux</td>
              <td>3000</td>
              <td>6000</td>
            </tr>
            <tr>
              <th scope="row">Hotel 4</th>
              <td>Family Room</td>
              <td></td>
              <td>10000</td>
            </tr>
            <tr>
              <th scope="row">Hotel 5</th>
              <td>Executive Non A/C</td>
              <td>1500</td>
              <td>3000</td>
            </tr>
            <tr>
              <th scope="row">Hotel 6</th>
              <td>Executive A/C</td>
              <td>2000</td>
              <td>5000</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default Trip;
